.header {
  background-color: #2b3643;
  height: 48px; }
  .header .left-header {
    flex-basis: 240px;
    justify-content: space-between; }
    .header .left-header img {
      height: 32px;
      margin-left: 16px; }
    .header .left-header .menu-bar {
      background-color: transparent;
      height: 100%; }
      .header .left-header .menu-bar i {
        font-size: 24px;
        color: #a7b5c6; }
  .header .right-header {
    flex-basis: calc(100% - 240px);
    flex-direction: row-reverse;
    padding-right: 16px; }
    .header .right-header .bar {
      padding: 0 8px;
      height: 100%;
      min-width: 32px;
      cursor: pointer; }
      .header .right-header .bar:hover {
        background-color: #3f4f62; }
