.login-page {
  background-color: #364150;
  height: 100vh; }
  .login-page .login-box {
    margin: 0 auto;
    width: 400px;
    padding-top: 120px;
    position: relative; }
    .login-page .login-box .logo-box {
      text-align: center; }
      .login-page .login-box .logo-box .logo {
        width: 100px; }
    .login-page .login-box .login-form {
      background-color: #fff;
      margin: 40px auto 10px;
      padding: 16px 24px 32px 24px;
      position: relative; }
      .login-page .login-box .login-form h3 {
        text-align: center;
        font-size: 28px;
        font-weight: 600 !important;
        margin-bottom: 16px; }
      .login-page .login-box .login-form .form-actions {
        margin-top: 32px; }
        .login-page .login-box .login-form .form-actions .lbl-remember {
          margin-left: 24px; }
        .login-page .login-box .login-form .form-actions .forgot-pwd {
          float: right;
          font-size: 13px;
          margin-top: 11px; }
