form .form-group {
  padding: 8px;
  box-sizing: border-box;
  width: 100%; }
  form .form-group .form-control {
    background-color: #dde3ec;
    height: 40px;
    color: #8290a3;
    border: 1px solid #dde3ec;
    padding: 6px 12px;
    width: 100%;
    display: block;
    font-size: 14px;
    line-height: 1.42857;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none; }
  form .form-group .radio-box {
    display: flex;
    height: 64px; }
    form .form-group .radio-box .radio {
      display: flex;
      width: 64px;
      align-items: center;
      margin-right: 16px; }
      form .form-group .radio-box .radio input {
        width: 24px; }

form.light .form-group {
  box-sizing: border-box; }
  form.light .form-group .label {
    font-size: 14px;
    margin-bottom: 2px;
    display: block; }
  form.light .form-group .form-control {
    color: #333;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    height: 32px; }
    form.light .form-group .form-control:focus {
      border-color: #93a1bb; }
  form.light .form-group select {
    border-radius: 0px !important; }
  form.light .form-group textarea.form-control {
