.btn {
  border-style: none;
  line-height: 1.44;
  font-size: 14px;
  cursor: pointer; }
  .btn.btn-green {
    background-color: #32c5d2;
    color: #fff; }
  .btn.btn-blue {
    background-color: #337ab7;
    color: #fff; }
  .btn:disabled {
    background-color: #dde3ec;
    color: #8290a3; }
  .btn.small {
    padding: 5px 10px !important;
    font-size: 11px; }

a {
  cursor: pointer;
  color: #337ab7; }

a:hover {
  opacity: 0.8; }

label.checkbox {
  color: #8290a3;
  font-size: 13px;
  cursor: pointer;
  position: relative; }
  label.checkbox input {
    cursor: pointer;
    opacity: 0; }

label span {
  border: 1px solid #d9d9d9;
  background: 0 0;
  position: absolute;
  top: -2px;
  left: -8px;
  height: 18px;
  width: 18px; }

label.active span::after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid #888;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  content: "";
  position: absolute; }

.message {
