.pb-container {
  display: inline-block;
  text-align: center;
  position: relative;
  width: 100%;
  height: 100%; }

.pb-container .pb-button {
  background: transparent;
  color: currentColor;
  cursor: pointer;
  padding: 8px 16px;
  text-decoration: none;
  width: 100%;
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  transition: background-color 0.3s, width 0.3s, border-width 0.3s, border-color 0.3s, border-radius 0.3s;
  overflow: hidden; }

.pb-container .pb-button span {
  display: inherit;
  transition: opacity 0.3s 0.1s; }

.pb-container .pb-button svg {
  position: absolute; }

.pb-container .pb-button .pb-cross,
.pb-container .pb-button .pb-checkmark {
  top: -14px;
  width: 66px; }

.pb-container .pb-button .pb-progress-circle {
  left: 60px;
  width: 40px;
  top: 20px; }

.pb-container .pb-button svg path {
  opacity: 0;
  fill: none; }

.pb-container .pb-button svg.pb-progress-circle {
  animation: spin 0.9s infinite cubic-bezier(0.085, 0.26, 0.935, 0.71); }

.pb-container .pb-button svg.pb-progress-circle path {
  stroke: currentColor;
  stroke-width: 5; }

.pb-container .pb-button svg.pb-checkmark path,
.pb-container .pb-button svg.pb-cross path {
  stroke: #fff;
  stroke-linecap: round;
  stroke-width: 4; }

.pb-container.disabled .pb-button {
  cursor: not-allowed; }

.pb-container.loading .pb-button {
  width: 100%;
  height: 100%;
  border-width: 6.5px;
  border-color: #ddd;
  cursor: wait;
  background-color: transparent; }

.pb-container.loading .pb-button span {
  transition: all 0.15s;
  opacity: 0;
  visibility: hidden; }

.pb-container.loading .pb-button .pb-progress-circle > path {
  transition: opacity 0.15s 0.3s;
  opacity: 1; }

.pb-container.success .pb-button span {
  transition: all 0.15s;
  opacity: 0;
  display: none; }

.pb-container.success .pb-button .pb-checkmark > path {
  opacity: 1; }

.pb-container.error .pb-button {
  border-color: #ed5565;
  background-color: #ed5565; }

.pb-container.error .pb-button span {
  transition: all 0.15s;
  opacity: 0;
  display: none; }

.pb-container.error .pb-button .pb-cross > path {
  opacity: 1; }

@keyframes spin {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
    transform-origin: center center; }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
    transform-origin: center center; } }

.page-content .tab {
  margin-top: 16px; }
